import React from 'react';
import { useNavigate } from 'react-router-dom';
import './TodoForm.css';

function TodoForm({ title, textButton, clickButton, defaultTodoText }) {
	const navigate = useNavigate();
	const [newTodoValue, setNewTodoValue] = React.useState(defaultTodoText || '');

	const onChange = (event) => {
		setNewTodoValue(event.target.value);
	};
	const onCancel = () => {
		navigate('/');
	};
	const onSubmit = (event) => {
		event.preventDefault();
		clickButton(newTodoValue);
		navigate('/');
	};

	return (
		<form onSubmit={onSubmit}>
			<label>{title}</label>
			<textarea value={newTodoValue} onChange={onChange} placeholder="Ingrese el texto" />
			<div className="TodoForm-buttonContainer">
				<button
					type="button"
					className="TodoForm-button TodoForm-button--cancel"
					onClick={onCancel}
				>
					Cancelar
				</button>
				<button type="submit" className="TodoForm-button TodoForm-button--add">
					{textButton}
				</button>
			</div>
		</form>
	);
}

export { TodoForm };
